import React, { useState } from 'react'
import List from './List'
import Alert from './Alert'
import localstorage from './ctrlStorage'

function App() {
  const [editID, setEditID] = useState(null)
  // eslint-disable-next-line
  const [title, setTitle] = useState('')
  const [typeOfAction, setTypeOfAction] = useState('submit')
  const [input, setInput] = useState('')
  const [foodList, setFoodList] = useState(localstorage.getItemsFromStorage())
  const [showAlert, SetShowAlert] = useState({
    msg: '',
    show: false,
    typeAlert: '',
  })
  // const [displayOn,setDisplayOn]=useState(false)
  const removeItem = (id) => {
    const newFoodList = foodList.filter((item) => item.id !== id)
    setFoodList(newFoodList)
    const deleteItem = foodList.find((item) => item.id === id)
    console.log(deleteItem)
    localstorage.deleteItemStorage(deleteItem)
    SetShowAlert({ msg: 'item removed', show: true, typeAlert: '' })
  }
  const editItem = (id, title) => {
    setTypeOfAction('edit')
    setInput(title)
    const specificItem = foodList.find((item) => item.id === id)
    // console.log(specificItem)
    setEditID(specificItem.id)
    setTitle(specificItem.title)
  }
  const removeAllItems = () => {
    setFoodList([])
    localStorage.clear()
    SetShowAlert({ msg: 'items removed', show: true, typeAlert: '' })
  }
  const removeAlert = () => {
    SetShowAlert({ msg: '', show: false, tipeAlert: '' })
  }

  const handlesubmit = (e) => {
    e.preventDefault()
    if (typeOfAction === 'submit') {
      if (input !== '') {
        const food = {
          title: input,
          id: new Date().getTime().toString(),
        }
        setInput('')
        localstorage.storageItem(food)
        setFoodList(() => {
          return [...foodList, food]
        })
        SetShowAlert({ msg: 'item add', show: true, typeAlert: 'add' })
      } else {
        console.log('im here')
        SetShowAlert({ msg: 'enter a value', show: true, typeAlert: '' })
      }
    }
    if (typeOfAction === 'edit') {
      setFoodList(
        foodList.map((item) => {
          if (item.id === editID) {
            localstorage.updateItemStorage({ id: item.id, title: input })
            return { ...item, title: input }
          }
          return item
        })
      )

      setTitle('')
      setEditID(null)
      setTypeOfAction('submit')
      setInput('')
      SetShowAlert({ msg: 'item changed', show: true, typeAlert: 'add' })
    }
  }

  return (
    <section className='section-center'>
      <form className='grocery-form' onSubmit={handlesubmit}>
        <Alert {...showAlert} foodList={foodList} removeAlert={removeAlert} />
        <h3>grocery bud</h3>
        <div className='form-control'>
          <input
            type='text'
            className='grocery'
            placeholder='e.g. eggs'
            value={input}
            onChange={(e) => setInput(e.target.value)}
          />
          <button type='submit' className='submit-btn'>
            {typeOfAction}
          </button>
        </div>
      </form>
      <div
        className='grocery-container'
        style={{ display: `${foodList.length === 0 ? 'none' : 'block'}` }}
      >
        <List list={foodList} removeItem={removeItem} editItem={editItem} />
        <button className='clear-btn' onClick={removeAllItems}>
          clear all
        </button>
      </div>
    </section>
  )
}

export default App
