import React, { useEffect } from 'react'

const Alert = ({ msg, show, typeAlert, foodList, removeAlert }) => {
  useEffect(() => {
    const timeout = setTimeout(() => {
      removeAlert()
    }, 3000)
    return () => clearTimeout(timeout)
  }, [foodList, show, removeAlert])

  return (
    <p
      className={
        typeAlert === 'add' ? 'alert alert-success' : ' alert alert-danger'
      }
      style={{ display: `${show ? 'block' : 'none'}` }}
    >
      {msg}
    </p>
  )
}

export default Alert
